import PatientDetailsVue from "@/components/Orders/PatientDetails.vue";
import { createApp } from "vue";

export function PatientDialog (show: boolean | undefined, order: any) : void {

    console.log("PatientDialog")
    console.log(order)

    const rootContainer = document.getElementById('rootContainer')!;

    const app = createApp(PatientDetailsVue, {
        show: show,
        order: order
    });
    app.mount(rootContainer, false )

}
