<template>
  <ion-page>
    <ion-content class="ion-padding">
      <ion-modal :is-open="open">
        <ion-header>
          <ion-toolbar>
            <ion-title style="font-size: 20px; font-weight: 600"
              >Patient order</ion-title
            >
            <ion-buttons slot="end">
              <ion-button :strong="true" @click="handleDialog()">
                <IonIcon size="large" :icon="close"></IonIcon>
              </ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-list>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Tracking Number</ion-label
              >
              <ion-label slot="end">{{ order?.tracking_number }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Accession Number</ion-label
              >
              <ion-label slot="end">{{ order?.accession_number }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Patient Name</ion-label
              >
              <ion-label slot="end">
                {{
                  `${order?.client.first_name} ${
                    order?.client.middle_name != null ? order?.client.middle_name : ""
                  } ${order?.client.last_name}`
                }}
              </ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Specimen Type</ion-label
              >
              <ion-label slot="end">{{ order?.specimen }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Test(s) Ordered</ion-label
              >
              <ion-label slot="end">{{
                extractedTests(order?.test_types)
              }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Specimen Status</ion-label
              >
              <ion-label slot="end">{{
                order?.order_status == "specimen-not-collected"
                  ? "Specimen not collected"
                  : order?.order_status
              }}</ion-label>
            </ion-item>
          </ion-list>
        </ion-content>
        <ion-footer>
          <ion-toolbar color="light" style="padding: 5px">
            <ion-button
              @click="changeSpecimenStatus('accepted')"
              slot="end"
              size="large"
              color="success"
            >
              Accept
            </ion-button>
            <ion-button
              @click="changeSpecimenStatus('rejected')"
              slot="end"
              size="large"
              color="danger"
            >
              Reject
            </ion-button>
            <ion-button slot="end" size="large" color="primary">
              Add Test
            </ion-button>
          </ion-toolbar>
        </ion-footer>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>

<script lang="ts">
import { Client } from "@/composables/client";
import router from "@/router";
import { toastSuccess } from "@/utils/Alerts";
import {
  IonButtons,
  IonButton,
  IonIcon,
  IonModal,
  IonHeader,
  IonContent,
  IonToolbar,
  IonTitle,
  IonItem,
  IonLabel,
  IonFooter,
  IonList,
  IonPage,
} from "@ionic/vue";

import { close } from "ionicons/icons";

import { PropType, handleError } from "vue";

import { defineComponent } from "vue";

export default defineComponent({
  components: {
    IonPage,
    IonButtons,
    IonButton,
    IonIcon,
    IonModal,
    IonHeader,
    IonContent,
    IonToolbar,
    IonTitle,
    IonFooter,
    IonItem,
    IonLabel,
    IonList,
  },
  data(): any {
    return {
      open: true as boolean,
      close: close,
    };
  },
  props: {
    order: {
      required: true,
      type: Object as PropType<any>,
    },
    show: {
      required: true,
      default: false,
      type: Boolean as PropType<any>,
    },
  },
  methods: {
    extractedTests(tests: Array<any>): string {
      return tests.map((test) => test.name).join(", ");
    },
    handleDialog(): void {
      this.open = false;
    },
    async changeSpecimenStatus(status: string): Promise<void> {
      const client = Client();

      const res = await client.putJson(`order_statuses/${status}`, {
        order_id: this.order.id,
      });

      if (res?.ok) {
        toastSuccess(`Specimen ${status} successfully!`);

        router.push("/home");
      }
    },
  },
});
</script>
<style lang=""></style>
